/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"How She Does It","date":"23 Apr 2006","slug":"/2006-04-23-how-she-does-it/","tags":"Y2006"},"html":"<p>Another <a href=\"http://www.misbehaving.net/2006/04/sophie_vanderbr.html\" title=\"misbehaving.net: Sophie Vanderbroek\">working mother story</a>, this time on <a href=\"http://www.fastcompany.com/magazine/104/vandebroek.html\" title=\"How She Does It\">Sophie Vandebroek</a>, CTO of Xerox, whose husband died 10 years ago leaving her with three children. </p>\n<blockquote><p>How does she do it? By sticking to strict rules for travel, refusing relocations, and living simply. She hires someone to do laundry and grocery shopping, and doesn't sweat it if things don't go perfectly. \"So many things we worry about,\" she says, \"are not important.\" She keeps the family schedule uncluttered--only one sport or activity per kid per season--and chooses simple weekend activities and vacations, as well. She even keeps her hair short to make the morning routine quicker.</p>\n<p>At work, she instructs her assistant not to plan any meetings before 9 am or after 5:30 pm. When traveling, she avoids scheduling meetings before 10 am, so she can fly in and out the same day.</p>\n<p>And she always has accepted new jobs, no matter what crisis was unfolding at home. \"The more senior jobs you get, the easier it is,\" she says. \"You get less control over how busy you are, but you get more over decisions about when you're busy and how you're going to do things.\"</p>\n</blockquote>\n<p>That's the secret, just keep getting promoted until you control your own schedule. </p>"},"next":{"fields":{"title":"Freetime 2.0","date":"23 Apr 2006","slug":"/2006-04-23-freetime-20/"}},"previous":{"fields":{"title":"I Bake Cookies, Too","date":"23 Apr 2006","slug":"/2006-04-23-i-bake-cookies-too/"}}},{"node":{"fields":{"title":"Freetime 2.0","date":"23 Apr 2006","slug":"/2006-04-23-freetime-20/","tags":"Y2006"},"html":"<p>Christopher is all set for a week of freetime, like he did <a href=\"http://www.papascott.de/archives/2005/04/06/practice-for-freetime/\" title=\"PapaScott &raquo; Blog Archive &raquo; Practice for Freetime\">last year</a> with his Kindergarten group, ages 4 to 6. They'll again be at St. Peter Ording on the North Sea for 4 nights away from their parents. Since he's done this before, he's not apprehensive about it at all, and is almost looking forward to it. </p>\n<p>A trip like this is not a normal part of German kindergarten, but Christopher does not attend a normal kindergarten. Lisa at An American in Aachen has posted about her son's poor experience with German special education in <a href=\"http://an-american-in-aachen.blogspot.com/2006/04/kids-who-fall-through-canyon-sized.html\" title=\"An American in Aachen: Kids who fall through the (canyon-sized) cracks...\">\"Kids who fall through the (canyon-sized) cracks...\"</a>. Our experience has been extremely positive, although there are some major differences in our situation. For one thing we are dealing with a charity-run kindergarten and not a public school. It's run by the local chapter of <a href=\"http://www.lebenshilfe.de/\"><em>Lebenshilfe</em></a>, a very fine organization that primarily helps the \"intellectually disabled\" and their families. For another, we live in a fairly well-off suburban/rural county with more resources to devote to education than a typical city. And finally, Christopher was offered specific therapy for his speech problems, and in fact the state of Lower Saxony has made a priority of pre-school speech. Lisa's son suffers from dyslexia, and it sounds like Aachen has nothing specific to offer him other than the <em>Sonderschule</em>.</p>\n<p>But now our adventure with the German school system is about to begin. As I <a href=\"http://www.papascott.de/archives/2006/04/01/school-20/\">mentioned before</a>, our local <em>Sonderschule</em> has determined that Christopher belongs in a regular school. Everything we have heard about our local elementary school has been very good, and our initial contacts have been positive, and they seem to be willing to work with us and with Christopher. We'll find out soon enough. Einschuling, the celebration of the first day of school, is on 2 September.</p>"},"next":{"fields":{"title":"Google's China Problem","date":"22 Apr 2006","slug":"/2006-04-22-googles-china-problem/"}},"previous":{"fields":{"title":"How She Does It","date":"23 Apr 2006","slug":"/2006-04-23-how-she-does-it/"}}},{"node":{"fields":{"title":"Google's China Problem","date":"22 Apr 2006","slug":"/2006-04-22-googles-china-problem/","tags":"Y2006"},"html":"<p>An interesting and long article in the New York Times on <a href=\"http://www.nytimes.com/2006/04/23/magazine/23google.html?_r=2&pagewanted=1&th&emc=th&oref=slogin\" title=\"Google's China Problem (and China's Google Problem) - New York Times\">Google's China Problem (and China's Google Problem)</a>, which asks whether some forms of censorship and collaboration and less evil than others, and ends with the proposition that the biggest influence for democracy in China may be \"American Idol\". </p>\n<p>By the way, Google did notice that <a href=\"http://www.papascott.de/archives/2006/01/28/google-gone/\">I removed my AdSense ads</a>. They sent me an automated email with tips for increasing my revenue. I have the feeling though that boycotting (even a small part of) Google is like boycotting oxygen. You turn blue in the face and everyone else keeps on breathing anyway.</p>"},"next":{"fields":{"title":"New Phone","date":"21 Apr 2006","slug":"/2006-04-21-new-phone/"}},"previous":{"fields":{"title":"Freetime 2.0","date":"23 Apr 2006","slug":"/2006-04-23-freetime-20/"}}}],"pathPrefix":"","first":false,"last":false,"index":225,"pageCount":1101}}

/***/ })

